import React from "react";
import emailjs from '@emailjs/browser';
import deliveryTruck from "../images/deliveryTruck.png"
function ContactGmail(props){
    const {cartItems, modal, setModal} = props;
    const printerProduct = cartItems.reduce((acc,curr)=> acc + `Sn phm ${curr.name} x${curr.qty}, `,"")
    function sendEmail(e){
        e.preventDefault();
        const serviceId = "gmail";
        const template ="template";
        const key ="Stq_bZsUmRZOv05J-";
        emailjs.sendForm(serviceId, template, e.target, key)
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
      };
    return (
        <div className="overlay">
        <div className="contactFormModal">
            <div className="introductionModal">
            <button className="btnCloseModal" onClick={()=> setModal(!modal)}>X</button>
          <h1 className="centerAlign inline font-size18px">   <img src={deliveryTruck} className="inline" id="fastdelivery"/>Thanh ton khi giao hng</h1>
            </div>
           
    <form onSubmit={sendEmail}>
        <div  className="contactContainer"> 
           <div className="twoSide">
           <input type="text" placeholder="Tn" className="inputFormat" required name="Name"></input>
           <input type="tel" placeholder="S in Thoi" pattern="[0-9]{10}" className="inputFormat" required name="PhoneNumber"></input>
           </div>
           <div className="twoSide">
            <input type="text" placeholder="a Ch" className="inputFormat full" required name="Address" ></input>
            <input type="email" placeholder="Email" className="inputFormat"  name="Email"></input>
            </div>
            <input type="text"  className="inputFormat blockFull" readOnly={true} required name="ItemsRender" value={printerProduct}/>
            <input type="text" placeholder="Ghi ch v thng tin mn hng m bn  chn" className="inputFormat blockFull" required name="Message"></input>
            <input type="submit" className="inputFormat bgr submitBtn buttonContact mb-30 hoverEffect " value="Gi Thng Tin"></input>  
            

            </div>
            
            
         </form>
        </div>
        </div>
    )
}
export default ContactGmail