import React from "react";
import { Route, Routes, Link} from "react-router-dom"
function OilPage(props){
const {products} = props;
return (
<div className="content_Container">
    <div className="captionBox">
        <h3 className="captionTitle">Trang Web bn Go Sch Uy Tn Nht Vit Nam Th K 21</h3>
    </div>

    <div class="productContainer" id="riceSection">
    <h1 className="centerAlign mb-30" id="riceList">Du n cc loi</h1>
    <ul className="gridList">
        {products.filter((item)=> item.category ==="oil").map((items) => 

        (<li className="product-box" key={items.id}>
            <img src={items.image} alt="Sn Phm Du Sch Cht Lng Cao  c kim nh" class="product_Image"/>
            <div className="product_Info">
                <p className="productName">{items.name}</p>
                <p className="price">{items.price}</p>
                <div className="buttonContact mb-30 hoverEffect">
                <Link to={`/${items.englishName}`}> Chi tit sn phm</Link>
                </div>
            </div>
        </li>))}

     </ul>
    </div>
</div>
)
}
export default OilPage;
